import React, { useState, useCallback } from 'react';
import { useSelector } from 'react-redux';
import { Grid, Button, Dialog, makeStyles, Box, Typography } from '@material-ui/core';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import Alert from '@material-ui/lab/Alert';
import { Pagination } from '@material-ui/lab';

import {
  isKeyListEmpty,
  keysPagerOptionsSelector,
  keysRequestSelector,
  keysSearchSelector,
} from 'src/containers/Keys/selectors';
import { useContainerKeys } from 'src/containers/Keys/hooks';
import KeyEntryForm from 'src/components/KeyEntryForm';
import KeyItem from 'src/components/KeyItem';
import PerPageSelect from 'src/components/PerPageSelect';
import SearchInput from 'src/components/SearchInput';

export interface IPropsKeysList {
  archive?: boolean;
}

const KeysList: React.FC<IPropsKeysList> = (props) => {
  const styles = useStyles();
  const { loading, data, error, pager } = useSelector(keysRequestSelector);
  const pagerOptions = useSelector(keysPagerOptionsSelector);
  const search = useSelector(keysSearchSelector);
  const isEmpty = useSelector(isKeyListEmpty);

  const {
    isFormOpened,
    handleModalToggle,
    handlePagerChange,
    handleChangePerPage,
    handleChangeSearch,
  } = useHooks();

  if (loading) {
    return null;
  }

  if (error) {
    return <Alert>{error}</Alert>;
  }

  return (
    <Box className={styles.wrap}>
      <Grid container spacing={2}>
        <Grid item container spacing={2} xs={12} alignItems={'center'}>
          {!props.archive && (
            <Grid item>
              <Button
                variant="outlined"
                startIcon={<AddCircleOutlineIcon />}
                onClick={handleModalToggle}
              >
                Add key entry
              </Button>
            </Grid>
          )}
          { !isEmpty &&
            <Grid item>
              <SearchInput
                defaultValue={search}
                onThrottledChange={handleChangeSearch}
                placeholder={'Search by name...'}
              />
            </Grid>
          }
        </Grid>
        {isEmpty && !props.archive &&
           <Grid item xs={12} className={styles.noItems}>
             <Typography variant="h5">There is no key entries. Add your first one!</Typography>
           </Grid>
        }
        {isEmpty && props.archive &&
           <Grid item xs={12} className={styles.noItems}>
             <Typography variant="h5">Archive is empty!</Typography>
           </Grid>
        }
        {data.items.length > 0 && (
          <Grid item xs={12}>
            <Grid container spacing={2}>
              {data.items.map((item) => {
                return (
                  <Grid item key={item.id} xs={6}>
                    <KeyItem data={item} />
                  </Grid>
                );
              })}
            </Grid>
          </Grid>
        )}
        {pager.totalPages !== undefined && pager.totalPages > 0 && (
          <Grid container item xs={12} alignItems="center">
            <Grid item>
              <PerPageSelect
                current={pager.perPage}
                options={pagerOptions}
                onSelect={handleChangePerPage}
              />
            </Grid>
            <Grid item className={styles.pagerHolder}>
              {pager.totalPages > 1 && (
                <Pagination
                  onChange={handlePagerChange}
                  count={pager.totalPages}
                  page={pager.page}
                />
              )}
            </Grid>
          </Grid>
        )}
      </Grid>
      <Dialog open={isFormOpened} onClose={handleModalToggle}>
        <KeyEntryForm onSubmit={handleModalToggle} />
      </Dialog>
    </Box>
  );
};

const useHooks = () => {
  const [isFormOpened, toggleForm] = useState(false);
  const { changePager, changeSearch } = useContainerKeys();

  const handleModalToggle = useCallback(() => {
    toggleForm(!isFormOpened);
  }, [isFormOpened, toggleForm]);

  const handlePagerChange = useCallback(
    (_event: React.ChangeEvent<unknown>, value: number) => {
      return changePager({ page: value });
    },
    [changePager],
  );

  const handleChangePerPage = useCallback(
    (val: number) => {
      return changePager({ perPage: val });
    },
    [changePager],
  );

  const handleChangeSearch = useCallback(
    (val: string) => {
      return changeSearch({ search: val });
    },
    [changeSearch],
  );

  return {
    isFormOpened,
    toggleForm,
    handleChangePerPage,
    handleChangeSearch,
    handleModalToggle,
    handlePagerChange,
  };
};

const useStyles = makeStyles(() => {
  return {
    pagerHolder: {
      marginLeft: 'auto',
    },
    searchHolder: {
      marginLeft: 'auto',
      marginTop: 'auto',
    },
    wrap: {
      maxWidth: '100%',
      flexGrow: 1,
    },
    noItems: {
      marginTop: 20,
    },
  };
});

export default KeysList;
